html, body {
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
}

.components {
  box-sizing: border-box;
  background-color: #a9a9a9;
  align-items: center;
  width: 100px;
  max-height: 440px;
  padding: 40px 0;
  display: flex;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.image-container {
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: auto;
  height: 300px;
  transition: transform .3s;
  display: grid;
  overflow-y: hidden;
}

.components img {
  cursor: pointer;
  width: 100px;
}

.components:before {
  content: "";
  position: inherit;
  border: 15px solid #0000;
  border-bottom-color: #d3d3d3;
  width: 0;
  height: 0;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
}

.components:after {
  content: "";
  position: inherit;
  border: 15px solid #0000;
  border-top-color: #d3d3d3;
  width: 0;
  height: 0;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
}

.run {
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  cursor: pointer;
  background-color: #90ee90;
  border: none;
  border-radius: 8px;
  outline: none;
  padding: 10px 20px;
  font-size: 20px;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

#popupDiv {
  background-color: #3333336c;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

